@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: 'Poppins', sans-serif;
}

.App {
  background-color: #fff;
  min-height: 100vh;
}

.wrapper{
  max-width: 600px;
  margin: auto;
}

.btn {
  background-color: #8ED0F9;
  border-radius: 15px;
  border: none;
  color: white;
  outline: none;
  grid-column: 3/4;
  justify-self: end;
  font-weight: bold;
  width: 120px;
}

.btn:active{
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  transition: ease-in-out 150ms;
}

.btn-secondary{
  background: #df1a1a;
  margin-right: 15px;
  padding: 8px 0;
  width: 100px;
}

.content-header{
  border: 1px solid #EBEEF0;
  color: rgb(24, 24, 24);
  display: -webkit-flex;
  display: flex;
  padding: 0 20px;
  margin-top: 16px;
  font-size: .75em;
}
.form{
  border: 1px solid #EBEEF0;
  border-top: none;
  display: grid;
  grid-template-columns: 75px 1fr 1fr;
  grid-template-rows: 30px 30px;
  grid-row-gap: 10px;
  row-gap: 10px;
  padding: 20px;
  margin-bottom: 20px;
}

.form img {
  grid-column: span 1;
  grid-row: span 1;
  border-radius: 50%;
  border: 1px solid #EBEEF0;
}

.form input {
  grid-column: span 2;
  grid-row: span 1;
  border-radius: 4px;
  padding: 14px 16px;
  border: none;
  outline: none;
}


.coment-content{
  border: 1px solid #EBEEF0;
  display: grid;
  grid-template-columns: .3fr 1fr;
  grid-template-rows: 30px 1fr;
  grid-row-gap: 8px;
  row-gap: 8px;
  padding: 24px 0px;
}

.coment-content img{
  -webkit-align-self: center;
          align-self: center;
  background-color: rgb(128, 126, 126);
  border-radius: 50%;
  justify-self: center;
}

.coment-content h3 {
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
}

.coment-content p {
  grid-column: 2/3 ;
  margin: 0;

}
